.list {
  color: #fff;
  width: 300px;
  background-color: #bf07ac;
  height: 40px;
  margin: 20px auto;
  border-radius: 5px;
}

.list p {
  padding: 10px;
  position: relative;
}

.list p input {
  background: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
  font-weight: bolder;
}

.trashicon {
  margin: 0 5px;
  font-size: 15px;
}

.list p input:focus {
  outline: 0;
}