* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  color: #333;
}

body {
  background: #9266ff;
}

header {
  background: rgba(63,49,96,1);
}

.app {
  background: rgba(63,49,96,.9);
  max-height: 500px;
  min-height: 500px;
  width: 90%;
  margin: 40px auto;
  border-radius: 10px;
  overflow: scroll;
}

@media screen and (min-width: 600px) {
  .app {
  background: #3f3160;
  min-height: 600px;
  width: 90%;
  margin: 40px auto;
  border-radius: 10px;
  overflow: scroll;
}

.to-do-form {
  margin: 0 80px;
  width: 60%;
}

.to-do-form input {
  background: rgb(27, 112, 137);
  height: 50px;
  width: 60%;
  border: 0;
  padding: 0px 20px;
  margin: 20px;
  font-size: 18px;
  color: #fff;
  border-radius: 10px;
}

}

.to-do-form input {
  background: rgb(27, 112, 137);
  height: 40px;
  width: 200px;
  border: 0;
  padding: 0px 20px;
  margin: 20px;
  font-size: 18px;
  color: #fff;
  border-radius: 10px;
}

.to-do-form input::placeholder {
  color: rgba(255, 255, 255, .5);
  border: 0;
}

.to-do-form input:focus, .to-do-form button:focus {
  outline: none;
}

.to-do-form button {
  height: 40px;
  width: 70px;  
  border-radius: 10px;
  border: 0;
  color: #fff;
  font-weight: bolder;
  background: rgb(202, 156, 24);
  cursor: pointer;
}